.aside {
    position: fixed;
    left: 0;
    top: 0;
    background-color: #fff;
    border-right: 1px solid rgba(0, 0, 0, 0.5);
    padding: 2.5rem;
    width: 110px;
    min-height: 100vh;
    color: #2f3e58;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 10;
    transition: .3s;
}

.nav__list {
    display: flex;
    flex-direction: column;
    row-gap: 1;
}

.nav__list li {
    margin-top: 20px;
}

.nav__link {
    font-size: 1.5rem;
    color: #2f3e58;
    font-weight: var(--font-bold);
}

.copyright {
    color: hsl(245, 15%, 65%);
    font-size: var(--small-font-size);
    transform: rotate(-180deg);
    writing-mode: vertical-rl;
}

.nav__toggle {
    position: fixed;
    top: 1.25rem;
    left: 1.875rem;
    font-size: 2.5rem;
    cursor: pointer;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    transition: .3s;
}


@media screen and (max-width: 1024px) {
    .aside {
        left: -110px;
    }

    .nav__toggle {
        display: flex;
    }

    .nav__toggle-open {
        left: 140px;
    }

    .show-menu {
        left: 0;
    }
}