.contact__container {
    grid-template-columns: 4fr 8fr;
    column-gap: 1.875rem;
}

.contact__items {
    margin-top: 4rem;
}

.contact__item {
    display: flex;
    align-items: center;
    margin-bottom: 2rem;
}

.contact__item ion-icon {
    font-size: 2rem;
    margin-right: 0.5rem;
    color: #ffd15c;
}

.contact__item span {
    line-height: 1.2;
}

.contact__item span a {
    color: #fff;
}

.contact__title {
    margin-bottom: 1rem;
}
.identity{
    display: flex;
}

.identity .input-box{
    margin: 0 5px;
    width: 100%;
}

.input-box {
    position: relative;
    margin: 25px 0;
}
.input-box input {
    width: 100%;
    height: 50px;
    background: transparent;
    border: 2px solid #2c4766;
    border-radius: 40px;
    font-size: 1em;
    color: #fff;
    padding: 0 20px;
    transition: 0.5s ease;
}
.input-box input:focus,
.input-box input:valid {
    border-color: #0ef;
}
.input-box label {
    position: absolute;
    top: 50%;
    left: 20px;
    transform: translateY(-50%);
    font-size: 1em;
    color: #fff;
    pointer-events: none;
    transition: 0.5s ease;
}
.input-box input:focus~label,
.input-box input:valid~label {
    top: 1px;
    font-size: 0.8em;
    background: #1f293a;
    padding: 0 6px;
    color: #0ef;
}

.input-box2 {
    display: flex;
    flex-direction: column;
}

.input-box2 textarea {
    padding: 10px;
    border-radius: 10px;
}

#inscriptionForm .btn{
    margin-top: 2rem;
    background-color: rgb(255, 76, 96);
    color: #fff;
}

@media screen and (max-width: 1024px) {
    .contact__title,
    .contact__details {
        text-align: center;
    }

}

@media screen and (max-width: 768px) {
    .contact__container {
        display: flex;
        flex-direction: column;
        row-gap: 1.875rem;
    }

    .contact__info {
        display: flex;
        flex-direction: column;
        align-items: center;

    }

    .identity {
        flex-direction: column;
    }

    .identity .input-box{
        margin: 0;
        margin-top: 20px;
    }
}