.work__filters {
    display: flex;
    align-items: center;
    column-gap: 1.875rem;
    margin-bottom: 2.5rem;
}

.work__img img {
    transform: scale(1.5);
}

.work__item {
    cursor: pointer;
    font-weight: var(--font-bold);
    border-radius: 20px;
    padding: 5px 10px;
    transition: .3s;
}

.work__item:hover {
    color: #28255d;
    background-color: #fff;
}

.work__container {
    grid-template-columns: repeat(4, 1fr);
    gap: 1.875rem;
}

.work__card {
    width: 250px;
    height: 140px;
    position: relative;
    overflow: hidden;
    border-radius: var(--border-radius);
    box-shadow: var(--shadow);
}

.work__mask {
    background: #6c6ce5;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    transition: .3s;
    opacity: 0;
}

.work__card:hover .work__mask {
    opacity: 0.9;
}

.work__category {
    color: #fff;
    background: rgb(255, 76, 96);
    border-bottom-left-radius: 0.98rem;
    border-bottom-right-radius: 0.98rem;
    position: absolute;
    top: 0;
    left: 1.5rem;
    font-size: var(--small-font-size);
    display: inline-block;
    padding: 0.19rem 0.625rem;
    transform: translateY(-40px);
    transition: .3s;
}

.work__title {
    color: #fff;
    font-size: 1rem;
    margin: 0 0 0 0.98rem;
    padding: 0 1.25rem;
    position: absolute;
    top: 3.75rem;
    transform: translateY(30px);
    transition: .3s;
    opacity: 0;
}

.work__button {
    color: #fff;
    position: absolute;
    bottom: 0.5rem;
    left: 1rem;
    font-size: 1.2rem;
    display: block;
    background: #ffd15c;
    height: 30px;
    width: 30px;
    cursor: pointer;
    border-radius: 50%;
    text-align: center;
    line-height: 35px;
    transition: .3s;
    opacity: 0;
}

.work__card:hover .work__button {
    opacity: 1;
}

.work__card:hover .work__title,
.work__card:hover .work__category {
    opacity: 1;
    transform: translateY(0);
}

@media screen and (max-width: 1024px) {
    .work__filters {
        justify-content: center;
    }

    .work__container {
        grid-template-columns: repeat(2, 330px);
        justify-content: center;
        row-gap: 1.875rem;
    }
}

@media screen and (max-width: 768px) {
    .work__container {
        grid-template-columns: 250px;
    }

    .work__filters {
        row-gap: .25rem;
    }
}

@media screen and (max-width: 576px) {
    .work__filters {
        flex-direction: column;
    }
}

