.home {
    background-image: url(../../assets/img/background-playstation-buttons.png);
    background-position: 10%;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.intro {
    background-color: rgba(56, 56, 56, 0.75);
    max-width: 540px;
    text-align: center;
    box-shadow: 0px 40px 40px 0px rgba(0, 0, 0, 0.5);
    padding: 30px;
    border-radius: 20px;
}

.home__img {
    max-width: 200px;
    height: auto;
    margin-bottom: 1.5rem;
}

.home__name {
    font-size: var(--h1-font-size);
    font-weight: var(--font-bold);
    margin-bottom: 0.5rem;
}

.home__socials {
    display: flex;
    justify-content: center;
    column-gap: 1.75rem;
    margin: 1.5rem 0;
}

.home__social-link {
    width: 50px;
    height: 50px;
    color: var(--text-color);
    font-size: 1.3rem;
    transition: .3s;
    border-radius: 50%;
    padding: 10px;
}

.home__social-link:hover {
    background: #e72a2a;
    border: 2px solid var(--text-color);
    transform: scale(1.21);
}

.scroll__down {
    position: absolute;
    bottom: 2rem;
    left: 0;
    width: 100%;
}

.home__scroll-name {
    font-size: var(--small-font-size);
    color: #a4a4a4;
}

.mouse {
    border: 2px solid var(--text-color);
    display: block;
    height: 1.6rem;
    width: 1.25rem;
    margin: auto;
    margin-top: .75rem;
    border-radius: 1rem;
    position: relative;
}

@keyframes ani-mouse {
    0% {
        top: 29%;
    }

    15% {
        top: 50%;
    }

    50% {
        top: 50%;
    }

    100% {
        top: 29%;
    }
}

.wheel {
    background-color: var(--text-color);
    border-radius: 100%;
    width: 0.25rem;
    height: 0.25rem;
    position: absolute;
    top: .5rem;
    left: 50%;
    transform: translateX(-50%);
    animation: ani-mouse 2s linear infinite;
}


@media screen and (max-width: 1024px) {
    .home__social-link {
        font-size: 1.125rem;
    }

    .intro {
        max-width: 320px;
    }
}